/* 作用：主体以及按需导入的第三方组件样式重写 */
/* 注意: 可导入到各组件中使用 */
.ant-form-item {
  margin-bottom: 8px;
}
.ant-table .operate-column {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.ant-table .operate-column__btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px;
}
.ant-modal {
  top: 20px;
  padding-bottom: 0;
}
.ant-modal-header {
  padding: 12px 24px;
}
.ant-modal-body {
  padding: 12px 24px;
  max-height: calc(100vh - 40px - 47px);
  overflow: hidden auto;
}
.ant-modal-close-x {
  width: 46px;
  height: 46px;
  line-height: 46px;
}
.ant-modal.full-height .ant-modal-body {
  height: calc(100vh - 40px - 47px);
}
.title {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.title__divide {
  width: 4px;
  height: 14px;
  margin-right: 8px;
  background-color: #4264D4;
}
.title__title {
  line-height: 1;
  font-size: 14px;
  color: #4264D4;
}
