/* 作用：主体以及按需导入的第三方组件样式重写 */
/* 注意: 可导入到各组件中使用 */
.ant-form-item {
  margin-bottom: 8px;
}
.ant-table .operate-column {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.ant-table .operate-column__btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px;
}
.ant-modal {
  top: 20px;
  padding-bottom: 0;
}
.ant-modal-header {
  padding: 12px 24px;
}
.ant-modal-body {
  padding: 12px 24px;
  max-height: calc(100vh - 40px - 47px);
  overflow: hidden auto;
}
.ant-modal-close-x {
  width: 46px;
  height: 46px;
  line-height: 46px;
}
.ant-modal.full-height .ant-modal-body {
  height: calc(100vh - 40px - 47px);
}
.paper-monitor {
  position: relative;
  height: 100%;
  background-color: #ebecf0;
}
.paper-monitor__sider {
  position: absolute;
  top: 0;
  left: 0;
  width: 300px;
  height: 100%;
  padding: 12px;
  background-color: #ffffff;
  box-shadow: 4px 4px 12px -2px #b2b9d3;
}
.paper-monitor__main {
  height: 100%;
  padding: 12px 12px 12px 312px;
  overflow: hidden auto;
}
.paper-monitor__machine {
  padding: 4px 12px;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 4px 4px 12px -2px #b2b9d3;
}
.paper-monitor__machine-item {
  font-size: 18px;
  color: #4264D4;
  padding: 12px 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.paper-monitor__switch {
  margin-top: 24px;
  height: 40px;
  border-radius: 20px;
  background-color: #ebecf0;
}
.paper-monitor__switch-item {
  float: left;
  width: 50%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 14px;
  color: #4264D4;
  border-radius: 20px;
  transition: all 0.2s ease;
  cursor: pointer;
}
.paper-monitor__switch-item.active {
  color: #ffffff;
  background-color: #40a9ff;
}
.paper-monitor__control-header {
  display: flex;
}
.paper-monitor__control-zone {
  flex: 1;
  padding: 12px;
  border-radius: 8px;
  background-color: #ffffff;
  box-shadow: 4px 4px 12px -2px #b2b9d3;
  color: #4264D4;
}
.paper-monitor__control-zone.disabled {
  color: #c6c6c6;
}
.paper-monitor__control-zone:nth-child(2) {
  margin: 0 6px 0 12px;
}
.paper-monitor__control-zone:nth-child(3) {
  margin: 0 12px 0 6px;
}
.paper-monitor__control-zone .collapse {
  transform: rotateZ(180deg);
}
.paper-monitor__control-img {
  height: 60vh;
  border-radius: 8px;
  margin: 12px 0;
  background-size: cover;
}
.paper-monitor__control-mv {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}
.paper-monitor__control-mvZone {
  width: 24%;
  padding: 12px;
  border-radius: 8px;
  background-color: #ffffff;
  box-shadow: 4px 4px 12px -2px #b2b9d3;
  color: #4264D4;
}
.paper-monitor__chart {
  padding: 12px;
  border-radius: 8px;
  background-color: #ffffff;
}
