/* 作用：主体以及按需导入的第三方组件样式重写 */
/* 注意: 可导入到各组件中使用 */
.ant-form-item {
  margin-bottom: 8px;
}
.ant-table .operate-column {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.ant-table .operate-column__btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px;
}
.ant-modal {
  top: 20px;
  padding-bottom: 0;
}
.ant-modal-header {
  padding: 12px 24px;
}
.ant-modal-body {
  padding: 12px 24px;
  max-height: calc(100vh - 40px - 47px);
  overflow: hidden auto;
}
.ant-modal-close-x {
  width: 46px;
  height: 46px;
  line-height: 46px;
}
.ant-modal.full-height .ant-modal-body {
  height: calc(100vh - 40px - 47px);
}
.home {
  height: 100%;
  padding: 12px;
  background-color: #ebecf0;
  overflow: hidden auto;
}
.home__paper {
  float: left;
  width: calc(50% - 24px);
  padding: 24px;
  margin: 12px;
  border-radius: 8px;
  box-shadow: inset 0 0 16px rgba(0, 0, 0, 0.18);
}
.home__paper-overview {
  height: 240px;
}
.home__paper-pic {
  float: left;
  width: 40%;
  height: 240px;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 4px 4px 12px -2px #b2b9d3;
}
.home__paper-pic--bg {
  height: 200px;
  background-size: cover;
}
.home__paper-pic--more {
  position: relative;
  height: 40px;
  padding: 0 12px;
  background-color: #fff;
  font-size: 14px;
  line-height: 1;
  color: #4264D4;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.home__paper-pic--status {
  position: absolute;
  top: 10px;
  right: 12px;
}
.home__paper-vars.overview {
  float: left;
  width: calc(60% - 12px);
  margin-left: 12px;
}
.home__paper-vars.overview .home__paper-vars--zone {
  float: left;
  width: calc(50% - 6px);
}
.home__paper-vars.overview .home__paper-vars--zone:nth-child(odd) {
  margin-right: 6px;
}
.home__paper-vars.overview .home__paper-vars--zone:nth-child(even) {
  margin-left: 6px;
}
.home__paper-vars.overview .home__paper-vars--zone:nth-child(1),
.home__paper-vars.overview .home__paper-vars--zone:nth-child(2) {
  margin-bottom: 12px;
}
.home__paper-vars.overview .home__paper-vars--zone.is-name {
  background-color: transparent;
  box-shadow: none;
}
.home__paper-vars.overview .home__paper-vars--zone.is-name .ant-btn {
  height: 48px;
  border-radius: 24px;
}
.home__paper-vars.details {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 0 4px;
}
.home__paper-vars.details .home__paper-vars--zone {
  width: 31%;
  margin-bottom: 12px;
}
.home__paper-vars--zone {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 114px;
  color: #4264D4;
  border-radius: 8px;
  box-shadow: 4px 4px 12px -2px #b2b9d3;
  background-color: #ffffff;
}
.home__paper-detail {
  padding: 0 12px;
  margin-top: 12px;
  border-radius: 8px;
  background-color: #ffffff;
  box-shadow: 4px 4px 12px -2px #b2b9d3;
}
