/* 作用：主体以及按需导入的第三方组件样式重写 */
/* 注意: 可导入到各组件中使用 */
.ant-form-item {
  margin-bottom: 8px;
}
.ant-table .operate-column {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.ant-table .operate-column__btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px;
}
.ant-modal {
  top: 20px;
  padding-bottom: 0;
}
.ant-modal-header {
  padding: 12px 24px;
}
.ant-modal-body {
  padding: 12px 24px;
  max-height: calc(100vh - 40px - 47px);
  overflow: hidden auto;
}
.ant-modal-close-x {
  width: 46px;
  height: 46px;
  line-height: 46px;
}
.ant-modal.full-height .ant-modal-body {
  height: calc(100vh - 40px - 47px);
}
.chart-show__chart {
  position: relative;
  overflow: hidden;
}
.chart-show__chart-time {
  position: absolute;
  top: 30px;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 12px;
  line-height: 1;
}
.chart-show__chart-time span {
  padding: 0 8px;
}
.chart-show__slider {
  padding: 6px 30px 6px 60px;
}
.chart-show__slider .ant-slider {
  margin: 0;
}
.chart-show__checkbox {
  position: relative;
  height: 24px;
  padding: 0 30px 0 60px;
}
.chart-show__checkbox-all {
  position: absolute;
  top: 5px;
  left: 30px;
}
.chart-show__checkbox-point {
  width: 1px;
  height: 1px;
  position: absolute;
  top: 12px;
}
.chart-show__checkbox-point .ant-checkbox-wrapper {
  position: absolute;
  top: -7px;
  left: -6px;
}
.chart-show__checkbox .ant-checkbox-wrapper {
  line-height: 1;
  font-size: 0;
}
.chart-show__checkbox .ant-checkbox,
.chart-show__checkbox .ant-checkbox-inner {
  width: 14px;
  height: 14px;
}
