/* 作用：主体以及按需导入的第三方组件样式重写 */
/* 注意: 可导入到各组件中使用 */
.ant-form-item {
  margin-bottom: 8px;
}
.ant-table .operate-column {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.ant-table .operate-column__btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px;
}
.ant-modal {
  top: 20px;
  padding-bottom: 0;
}
.ant-modal-header {
  padding: 12px 24px;
}
.ant-modal-body {
  padding: 12px 24px;
  max-height: calc(100vh - 40px - 47px);
  overflow: hidden auto;
}
.ant-modal-close-x {
  width: 46px;
  height: 46px;
  line-height: 46px;
}
.ant-modal.full-height .ant-modal-body {
  height: calc(100vh - 40px - 47px);
}
.data-preview {
  width: 100%;
  height: 100%;
  min-width: 1300px;
  overflow: hidden auto;
  background-color: #ffffff;
  position: relative;
}
.data-preview__header {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.data-preview__header-form {
  display: flex;
  flex: 1;
  min-width: 1150px;
  padding: 12px;
  border-bottom: 1px solid #d7d7d7;
}
.data-preview__header-form:last-child {
  min-width: 510px;
}
.data-preview__tabs {
  padding: 0 12px;
}
.data-preview__form-input {
  width: 130px;
}
.data-preview__chart-preview {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  z-index: 10;
}
