/* 作用：主体以及按需导入的第三方组件样式重写 */
/* 注意: 可导入到各组件中使用 */
.ant-form-item {
  margin-bottom: 8px;
}
.ant-table .operate-column {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.ant-table .operate-column__btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px;
}
.ant-modal {
  top: 20px;
  padding-bottom: 0;
}
.ant-modal-header {
  padding: 12px 24px;
}
.ant-modal-body {
  padding: 12px 24px;
  max-height: calc(100vh - 40px - 47px);
  overflow: hidden auto;
}
.ant-modal-close-x {
  width: 46px;
  height: 46px;
  line-height: 46px;
}
.ant-modal.full-height .ant-modal-body {
  height: calc(100vh - 40px - 47px);
}
.collapse-zone {
  position: relative;
  display: flex;
}
.collapse-zone__left,
.collapse-zone__right {
  transition: width 0.2s ease-in-out;
}
.collapse-zone__left {
  border-right: 1px solid #d7d7d7;
}
.collapse-zone__right {
  position: relative;
  white-space: nowrap;
}
.collapse-zone__right-collapse {
  position: absolute;
  top: 12px;
  left: -25px;
  background-color: #ffffff;
  box-shadow: 0 0 10px 1px rgba(122, 0, 255, 0.3);
  width: 24px;
  height: 32px;
  display: flex;
  align-items: center;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  cursor: pointer;
}
