/* 作用：主体以及按需导入的第三方组件样式重写 */
/* 注意: 可导入到各组件中使用 */
.ant-form-item {
  margin-bottom: 8px;
}
.ant-table .operate-column {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.ant-table .operate-column__btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px;
}
.ant-modal {
  top: 20px;
  padding-bottom: 0;
}
.ant-modal-header {
  padding: 12px 24px;
}
.ant-modal-body {
  padding: 12px 24px;
  max-height: calc(100vh - 40px - 47px);
  overflow: hidden auto;
}
.ant-modal-close-x {
  width: 46px;
  height: 46px;
  line-height: 46px;
}
.ant-modal.full-height .ant-modal-body {
  height: calc(100vh - 40px - 47px);
}
.zone {
  position: relative;
  cursor: pointer;
}
.zone__content {
  transition: all 0.2s ease;
}
.zone__content::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  z-index: 1;
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.12);
  opacity: 0;
  transition: opacity 0.2s ease;
}
.zone__tool {
  position: absolute;
  top: 4px;
  right: 0;
  z-index: 2;
  padding: 4px 8px;
  line-height: 1;
  border-radius: 4px;
  background-color: #ffffff;
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.12);
  opacity: 0;
  transition: opacity 0.2s ease;
}
.zone__tool .anticon {
  font-size: 16px;
}
.zone:hover .zone__content::after,
.zone:hover .zone__tool {
  opacity: 1;
}
