/* 作用：主体以及按需导入的第三方组件样式重写 */
/* 注意: 可导入到各组件中使用 */
.ant-form-item {
  margin-bottom: 8px;
}
.ant-table .operate-column {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.ant-table .operate-column__btn {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px;
}
.ant-modal {
  top: 20px;
  padding-bottom: 0;
}
.ant-modal-header {
  padding: 12px 24px;
}
.ant-modal-body {
  padding: 12px 24px;
  max-height: calc(100vh - 40px - 47px);
  overflow: hidden auto;
}
.ant-modal-close-x {
  width: 46px;
  height: 46px;
  line-height: 46px;
}
.ant-modal.full-height .ant-modal-body {
  height: calc(100vh - 40px - 47px);
}
.control-monitor {
  width: 100%;
  height: 100%;
  min-width: 1300px;
  overflow: hidden auto;
  background-color: #ffffff;
  position: relative;
}
.control-monitor__header {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.control-monitor__header-form {
  display: flex;
  flex: 1;
  padding: 12px;
  min-width: 1140px;
  border-right: 1px solid #d7d7d7;
  border-bottom: 1px solid #d7d7d7;
}
.control-monitor__header-gif {
  position: relative;
  height: 32px;
  margin-right: 16px;
}
.control-monitor__header-gif img {
  width: auto;
  height: 100%;
}
.control-monitor__header-gif div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 32px;
  line-height: 32px;
  font-size: 12px;
  color: #949494;
  text-align: center;
}
.control-monitor__header-group {
  padding: 17px 12px;
  border-right: 1px solid #d7d7d7;
  border-bottom: 1px solid #d7d7d7;
}
.control-monitor__header-control {
  width: 270px;
  padding: 0 12px;
  border-bottom: 1px solid #d7d7d7;
}
.control-monitor__header-control .ant-divider {
  height: 100%;
  margin: 0 12px;
  border-color: #d7d7d7;
}
.control-monitor__header-control .ant-btn {
  margin: 12px 0;
}
.control-monitor__btn-group {
  position: relative;
  margin-top: 12px;
  padding: 8px 240px 8px 0;
  text-align: center;
  border-top: 1px solid #d7d7d7;
}
.control-monitor__btn-group > .ant-btn {
  margin: 0 18px;
}
.control-monitor__btn-group > .ant-btn:nth-child(1):not([disabled]) {
  background-color: #ffa800;
  color: #ffffff;
}
.control-monitor__btn-group > .ant-btn:nth-child(2):not([disabled]) {
  background-color: #00b929;
  color: #ffffff;
}
.control-monitor__btn-group--full {
  position: absolute;
  top: 0;
  right: 0;
  width: 240px;
  height: 49px;
}
.control-monitor__btn-group--full .ant-btn {
  width: 50%;
  height: 100%;
  border: none;
  border-radius: 0;
  font-size: 16px;
}
.control-monitor__btn-group--full .ant-btn:nth-child(1):not([disabled]) {
  background-color: #ffa800;
  color: #ffffff;
}
.control-monitor__btn-group--full .ant-btn:nth-child(2):not([disabled]) {
  background-color: #00b929;
  color: #ffffff;
}
.control-monitor__footer {
  position: relative;
  top: -1px;
  padding: 12px 24px 36px 24px;
  border-top: 1px solid #d7d7d7;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.control-monitor__footer-left,
.control-monitor__footer-right {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  line-height: 1;
  font-size: 14px;
}
.control-monitor__footer-left .status {
  display: inline-block;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  margin: 0 12px;
}
.control-monitor__footer-left .status.fine {
  background-color: green;
}
.control-monitor__footer-left .status.error {
  background-color: red;
}
.control-monitor__form-input {
  width: 130px;
}
.control-monitor.loop-set {
  min-width: unset;
}
.control-monitor.loop-set .control-monitor__select {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 200px;
}
